
import React from 'react'

var CheckBoxList=React.createClass({
    render: function(){
        return (
            <div className="widget-tools">
                <ul>
                    <li style={{'marginRight': '20px'}}><label></label></li>
                    <li><label><input type="checkbox"/><span>ADT</span></label></li>
                    <li><label><input type="checkbox"/><span></span></label></li>
                    <li><label><input type="checkbox"/><span></span></label></li>
                    <li><label><input type="checkbox"/><span></span></label></li>
                    <li><label><input type="checkbox"/><span></span></label></li>
                    <li><label><input type="checkbox"/><span></span></label></li>
                    <li><label><input type="checkbox"/><span></span></label></li>
                    <li><button className="btn-blue btn-xs" type="button">
                            <i className="iconfont icon-refresh"></i>
                        </button></li>
                    <li><button className="btn-blue btn-xs" type="button"></button></li>
                </ul>
                <div className="text-align-right">
                    <button className="btn-blue btn-sm" type="button"></button>
                </div>
            </div>
        );
    }
});
export default CheckBoxList;



